// front/src/services/questionsService.js - CORREO E MELHORIAS
import api from './api';

export const questionsService = {
  // Buscar perguntas de um anncio
  async getAdQuestions(adId) {
    try {
      console.log(' Buscando perguntas para anncio:', adId);

      if (!adId) {
        throw new Error('ID do anncio  obrigatrio');
      }

      const response = await api.get(`/ad-questions/ad/${adId}/questions`);

      console.log(' Resposta das perguntas:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Perguntas carregadas'
      };
    } catch (error) {
      console.error(' Erro ao buscar perguntas:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao carregar perguntas',
        data: { questions: [] }
      };
    }
  },

  // Fazer uma pergunta
  async askQuestion(adId, question, isPublic = true) {
    try {
      console.log(' Enviando pergunta:', { adId, question, isPublic });

      if (!adId || !question) {
        throw new Error('ID do anncio e pergunta so obrigatrios');
      }

      if (question.trim().length < 10) {
        throw new Error('Pergunta deve ter pelo menos 10 caracteres');
      }

      const requestData = {
        question: question.trim(),
        is_public: isPublic
      };

      console.log(' Dados da requisio:', requestData);

      const response = await api.post(`/ad-questions/ad/${adId}/question`, requestData);

      console.log(' Resposta do envio:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Pergunta enviada com sucesso'
      };
    } catch (error) {
      console.error(' Erro ao enviar pergunta:', error);

      // Log detalhado do erro
      if (error.response) {
        console.error('Dados da resposta de erro:', error.response.data);
        console.error('Status do erro:', error.response.status);
      }

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao enviar pergunta'
      };
    }
  },

  // Responder uma pergunta
  async answerQuestion(questionId, answer) {
    try {
      console.log(' Enviando resposta:', { questionId, answer });

      if (!questionId || !answer) {
        throw new Error('ID da pergunta e resposta so obrigatrios');
      }

      if (answer.trim().length < 5) {
        throw new Error('Resposta deve ter pelo menos 5 caracteres');
      }

      const requestData = {
        answer: answer.trim()
      };

      const response = await api.post(`/ad-questions/question/${questionId}/answer`, requestData);

      console.log(' Resposta do envio da resposta:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Resposta enviada com sucesso'
      };
    } catch (error) {
      console.error(' Erro ao enviar resposta:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao enviar resposta'
      };
    }
  },

  // Deletar uma pergunta
  async deleteQuestion(questionId) {
    try {
      console.log(' Deletando pergunta:', questionId);

      if (!questionId) {
        throw new Error('ID da pergunta  obrigatrio');
      }

      const response = await api.delete(`/ad-questions/question/${questionId}`);

      console.log(' Resposta da deleo:', response.data);

      return {
        success: response.data.success || true,
        message: response.data.message || 'Pergunta deletada com sucesso'
      };
    } catch (error) {
      console.error(' Erro ao deletar pergunta:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao deletar pergunta'
      };
    }
  },

  // Alterar visibilidade da pergunta
  async toggleQuestionVisibility(questionId) {
    try {
      console.log(' Alterando visibilidade:', questionId);

      if (!questionId) {
        throw new Error('ID da pergunta  obrigatrio');
      }

      const response = await api.put(`/ad-questions/question/${questionId}/visibility`);

      console.log(' Resposta da alterao:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Visibilidade alterada com sucesso'
      };
    } catch (error) {
      console.error(' Erro ao alterar visibilidade:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao alterar visibilidade'
      };
    }
  },

  // Buscar perguntas do usurio
  async getUserQuestions(type = 'asked') {
    try {
      console.log(' Buscando perguntas do usurio:', type);

      if (!['asked', 'answered'].includes(type)) {
        throw new Error('Tipo deve ser "asked" ou "answered"');
      }

      const response = await api.get(`/ad-questions/user/questions?type=${type}`);

      console.log(' Perguntas do usurio:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Perguntas carregadas'
      };
    } catch (error) {
      console.error(' Erro ao buscar perguntas do usurio:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao carregar perguntas',
        data: { questions: [] }
      };
    }
  },

  // Buscar perguntas dos meus anncios
  async getMyAdQuestions(params = {}) {
    try {
      console.log(' Buscando perguntas dos meus anncios:', params);

      const response = await api.get('/ad-questions/my-questions', { params });

      console.log(' Perguntas dos meus anncios:', response.data);

      return {
        success: response.data.success || true,
        data: response.data.data || response.data,
        message: response.data.message || 'Perguntas carregadas'
      };
    } catch (error) {
      console.error(' Erro ao buscar perguntas dos meus anncios:', error);

      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Erro ao carregar perguntas',
        data: { questions: [] }
      };
    }
  },

  // Funo de debug para verificar conectividade
  async debugConnection() {
    try {
      console.log(' Testando conexo com API de perguntas...');

      // Fazer uma requisio simples para testar
      const response = await api.get('/health');

      console.log(' Conexo OK:', response.data);

      return {
        success: true,
        message: 'Conexo estabelecida',
        data: response.data
      };
    } catch (error) {
      console.error(' Falha na conexo:', error);

      return {
        success: false,
        message: 'Falha na conexo com a API',
        error: error.message
      };
    }
  },

  // Validar dados antes de enviar
  validateQuestionData(question, isPublic) {
    const errors = [];

    if (!question || typeof question !== 'string') {
      errors.push('Pergunta deve ser um texto vlido');
    } else {
      const trimmed = question.trim();
      if (trimmed.length < 10) {
        errors.push('Pergunta deve ter pelo menos 10 caracteres');
      }
      if (trimmed.length > 500) {
        errors.push('Pergunta no pode ter mais de 500 caracteres');
      }
    }

    if (typeof isPublic !== 'boolean') {
      errors.push('Visibilidade deve ser verdadeiro ou falso');
    }

    return {
      isValid: errors.length === 0,
      errors,
      data: {
        question: question?.trim(),
        is_public: isPublic
      }
    };
  },

  // Validar dados da resposta
  validateAnswerData(answer) {
    const errors = [];

    if (!answer || typeof answer !== 'string') {
      errors.push('Resposta deve ser um texto vlido');
    } else {
      const trimmed = answer.trim();
      if (trimmed.length < 5) {
        errors.push('Resposta deve ter pelo menos 5 caracteres');
      }
      if (trimmed.length > 1000) {
        errors.push('Resposta no pode ter mais de 1000 caracteres');
      }
    }

    return {
      isValid: errors.length === 0,
      errors,
      data: {
        answer: answer?.trim()
      }
    };
  }
};