import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';
import { useAuth } from '../../contexts/AuthContext';
import { chatService } from '../../services/chatService';
import {
  Send,
  User,
  MessageCircle,
  AlertCircle,
  Loader,
  Clock,
  CheckCircle2,
  Phone,
  Video,
  Settings
} from 'lucide-react';
import Button from '../Common/Button';

const ChatRoom = ({ orderId, onClose }) => {
  const { user } = useAuth();
  const [room, setRoom] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(true);
  const [connected, setConnected] = useState(false);
  const [sending, setSending] = useState(false);
  const [typingUsers, setTypingUsers] = useState(new Set());
  const [socket, setSocket] = useState(null);
  
  const messagesEndRef = useRef(null);
  const typingTimeoutRef = useRef(null);
  const messageInputRef = useRef(null);

  useEffect(() => {
    initializeChat();
    return () => {
      cleanup();
    };
  }, [orderId]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const initializeChat = async () => {
    setLoading(true);

    try {
      // Buscar sala de chat
      const roomResult = await chatService.getOrderChatRoom(orderId);
      if (!roomResult.success) {
        toast.error(roomResult.message);
        return;
      }

      setRoom(roomResult.data.room);

      // Conectar ao WebSocket
      const token = localStorage.getItem('access_token') || document.cookie
        .split('; ')
        .find(row => row.startsWith('access_token='))
        ?.split('=')[1];

      if (token) {
        const socketInstance = chatService.connect(token);
        setSocket(socketInstance);

        setupSocketListeners(socketInstance, roomResult.data.room._id);
        
        // Entrar na sala
        socketInstance.emit('join_chat_room', { room_id: roomResult.data.room._id });
      }

    } catch (error) {
      console.error('Erro ao inicializar chat:', error);
      toast.error('Erro ao carregar chat');
    }

    setLoading(false);
  };

  const setupSocketListeners = (socketInstance, roomId) => {
    socketInstance.on('connected', () => {
      setConnected(true);
      console.log('Conectado ao chat');
    });

    socketInstance.on('room_joined', (data) => {
      console.log('Entrou na sala:', data);
      setMessages(data.messages || []);
    });

    socketInstance.on('new_message', (data) => {
      const message = data.message;
      if (message.room_id === roomId) {
        setMessages(prev => [...prev, message]);
        
        // Tocar som de notificao se no for prpria mensagem
        if (message.user_id !== user._id) {
          playNotificationSound();
        }
      }
    });

    socketInstance.on('user_typing', (data) => {
      if (data.user_id !== user._id) {
        setTypingUsers(prev => new Set([...prev, data.username]));
        
        // Remover aps 3 segundos
        setTimeout(() => {
          setTypingUsers(prev => {
            const newSet = new Set(prev);
            newSet.delete(data.username);
            return newSet;
          });
        }, 3000);
      }
    });

    socketInstance.on('user_stop_typing', (data) => {
      if (data.user_id !== user._id) {
        setTypingUsers(prev => {
          const newSet = new Set(prev);
          newSet.delete(data.username);
          return newSet;
        });
      }
    });

    socketInstance.on('system_notification', (data) => {
      toast.info(data.message);
    });

    socketInstance.on('error', (error) => {
      console.error('Erro no WebSocket:', error);
      toast.error(error.message || 'Erro na conexo');
    });

    socketInstance.on('disconnect', () => {
      setConnected(false);
      console.log('Desconectado do chat');
    });
  };

  const cleanup = () => {
    if (socket) {
      if (room) {
        socket.emit('leave_chat_room', { room_id: room._id });
      }
      chatService.disconnect();
    }
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const playNotificationSound = () => {
    try {
      const audio = new Audio('/notification.mp3');
      audio.volume = 0.3;
      audio.play().catch(() => {
        // Ignorar erro se no conseguir tocar
      });
    } catch (error) {
      // Ignorar erro de som
    }
  };

  const handleSendMessage = async (e) => {
    e.preventDefault();
    
    if (!newMessage.trim() || sending || !connected) return;

    setSending(true);

    try {
      // Enviar via WebSocket
      if (socket && room) {
        socket.emit('send_message', {
          room_id: room._id,
          content: newMessage.trim()
        });
        
        setNewMessage('');
        messageInputRef.current?.focus();
      }
    } catch (error) {
      console.error('Erro ao enviar mensagem:', error);
      toast.error('Erro ao enviar mensagem');
    }

    setSending(false);
  };

  const handleTyping = () => {
    if (socket && room && connected) {
      socket.emit('typing', { room_id: room._id });
      
      // Parar de indicar typing aps 2 segundos
      if (typingTimeoutRef.current) {
        clearTimeout(typingTimeoutRef.current);
      }
      
      typingTimeoutRef.current = setTimeout(() => {
        socket.emit('stop_typing', { room_id: room._id });
      }, 2000);
    }
  };

  const formatTime = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const isToday = date.toDateString() === now.toDateString();
    
    if (isToday) {
      return date.toLocaleTimeString('pt-BR', {
        hour: '2-digit',
        minute: '2-digit'
      });
    } else {
      return date.toLocaleDateString('pt-BR', {
        day: '2-digit',
        month: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  };

  const getMessageStatus = (message) => {
    if (message.user_id === user._id) {
      return message.read_by?.length > 1 ? 'read' : 'sent';
    }
    return null;
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        <div className="text-center">
          <Loader className="w-8 h-8 animate-spin text-blue-600 mx-auto mb-2" />
          <p className="text-gray-600">Carregando chat...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-96 bg-white rounded-lg shadow-md">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b border-gray-200">
        <div className="flex items-center space-x-3">
          <MessageCircle className="w-5 h-5 text-blue-600" />
          <div>
            <h3 className="font-semibold text-gray-800">Chat do Pedido</h3>
            <div className="flex items-center space-x-2">
              <div className={`w-2 h-2 rounded-full ${connected ? 'bg-green-500' : 'bg-red-500'}`}></div>
              <span className="text-xs text-gray-600">
                {connected ? 'Conectado' : 'Desconectado'}
              </span>
            </div>
          </div>
        </div>

        <div className="flex items-center space-x-2">
          <button
            className="p-2 text-gray-400 hover:text-gray-600 rounded-full hover:bg-gray-100"
            title="Chamada de voz (em breve)"
          >
            <Phone className="w-4 h-4" />
          </button>
          <button
            className="p-2 text-gray-400 hover:text-gray-600 rounded-full hover:bg-gray-100"
            title="Chamada de vdeo (em breve)"
          >
            <Video className="w-4 h-4" />
          </button>
          <button
            className="p-2 text-gray-400 hover:text-gray-600 rounded-full hover:bg-gray-100"
            title="Configuraes"
          >
            <Settings className="w-4 h-4" />
          </button>
        </div>
      </div>

      {/* Status da conexo */}
      {!connected && (
        <div className="px-4 py-2 bg-yellow-50 border-b border-yellow-200">
          <div className="flex items-center text-yellow-800">
            <AlertCircle className="w-4 h-4 mr-2" />
            <span className="text-sm">Reconectando ao chat...</span>
          </div>
        </div>
      )}

      {/* Mensagens */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.length === 0 ? (
          <div className="text-center py-8">
            <MessageCircle className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-600">Nenhuma mensagem ainda</p>
            <p className="text-sm text-gray-500">
              Inicie a conversa sobre este pedido
            </p>
          </div>
        ) : (
          messages.map((message, index) => {
            const isOwnMessage = message.user_id === user._id;
            const isSystemMessage = message.is_system;
            
            return (
              <div
                key={message._id || index}
                className={`flex ${
                  isSystemMessage 
                    ? 'justify-center' 
                    : isOwnMessage 
                    ? 'justify-end' 
                    : 'justify-start'
                }`}
              >
                {isSystemMessage ? (
                  <div className="bg-blue-50 text-blue-800 px-3 py-2 rounded-lg text-sm border border-blue-200">
                    {message.content}
                  </div>
                ) : (
                  <div className={`max-w-xs lg:max-w-md ${isOwnMessage ? 'ml-12' : 'mr-12'}`}>
                    {!isOwnMessage && message.user && (
                      <div className="flex items-center mb-1">
                        <div className="w-6 h-6 bg-gray-300 rounded-full flex items-center justify-center mr-2">
                          {message.user.profile_pic ? (
                            <img
                              src={message.user.profile_pic}
                              alt={message.user.username}
                              className="w-6 h-6 rounded-full object-cover"
                            />
                          ) : (
                            <User className="w-3 h-3 text-gray-600" />
                          )}
                        </div>
                        <span className="text-xs font-medium text-gray-700">
                          {message.user.first_name || message.user.username}
                        </span>
                      </div>
                    )}
                    
                    <div
                      className={`px-4 py-2 rounded-lg ${
                        isOwnMessage
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-100 text-gray-900'
                      }`}
                    >
                      <p className="text-sm whitespace-pre-wrap">{message.content}</p>
                      
                      <div className={`flex items-center justify-between mt-1 ${
                        isOwnMessage ? 'text-blue-100' : 'text-gray-500'
                      }`}>
                        <span className="text-xs">
                          {formatTime(message.created_at)}
                        </span>
                        
                        {isOwnMessage && (
                          <div className="ml-2">
                            {getMessageStatus(message) === 'read' ? (
                              <CheckCircle2 className="w-3 h-3" />
                            ) : (
                              <Clock className="w-3 h-3" />
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                )}
              </div>
            );
          })
        )}

        {/* Indicador de digitao */}
        {typingUsers.size > 0 && (
          <div className="flex justify-start">
            <div className="max-w-xs lg:max-w-md mr-12">
              <div className="bg-gray-100 text-gray-600 px-4 py-2 rounded-lg">
                <div className="flex items-center space-x-1">
                  <span className="text-sm">
                    {Array.from(typingUsers).join(', ')} {typingUsers.size === 1 ? 'est' : 'esto'} digitando
                  </span>
                  <div className="flex space-x-1">
                    <div className="w-1 h-1 bg-gray-400 rounded-full animate-bounce"></div>
                    <div className="w-1 h-1 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-1 h-1 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      {/* Input de mensagem */}
      <div className="border-t border-gray-200 p-4">
        <form onSubmit={handleSendMessage} className="flex space-x-2">
          <input
            ref={messageInputRef}
            type="text"
            value={newMessage}
            onChange={(e) => {
              setNewMessage(e.target.value);
              handleTyping();
            }}
            placeholder={connected ? "Digite sua mensagem..." : "Aguardando conexo..."}
            className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-100"
            disabled={!connected}
            maxLength={1000}
          />
          <Button
            type="submit"
            disabled={!newMessage.trim() || sending || !connected}
            loading={sending}
            size="sm"
            className="px-4"
          >
            <Send className="w-4 h-4" />
          </Button>
        </form>
        
        {newMessage.length > 800 && (
          <p className="text-xs text-gray-500 mt-1">
            {newMessage.length}/1000 caracteres
          </p>
        )}
      </div>
    </div>
  );
};

export default ChatRoom;